body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

navbar {
  z-index: 10;
  position: absolute;
  top: 0;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}

navbar ul {
  margin: 0;
}

navbar li {
  display: inline;
  padding-right: 1.5vw;
  font-size: 2vw;
}

navbar a {
  text-decoration: none;
  color: #000;
}

navbar a:hover {
  text-decoration: underline;
}

.App {
  text-align: center;
}

.App-header {
  background: url(/static/media/splash-asset.7cde3828.jpg) no-repeat center center;
  background-size: contain;
  width: 100%;
  height: 0;
  padding-top: 56.9%;
  margin-top: -1px;
  text-align: center;
  position: relative;
}

.splash-text {
  position: absolute;
  top: 39%;
  vertical-align: middle;
  margin: auto;
  width: 100%;
  font-size: 6vw;
  font-family: tahoma;
  color: #fff;
}

.blurb-one {
  position: absolute;
  top: 15%;
  left: 10%;
  font-size: 1.5vw;
  font-family: tahoma;
  color: #fff;
}

.blurb-two {
  position: absolute;
  top: 25%;
  right: 10%;
  font-size: 1.5vw;
  font-family: tahoma;
  color: #fff;
}

.blurb-three {
  position: absolute;
  top: 75%;
  right: 30%;
  font-size: 1.5vw;
  font-family: tahoma;
  color: #fff;
}

@keyframes flickerAnimation {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100% { opacity:0; }
}

@-webkit-keyframes flickerAnimation{
  0%   { opacity:0; }
  50%  { opacity:1; }
  100% { opacity:0; }
}

.blurb-one {
   -webkit-animation: flickerAnimation 10s infinite;
    animation: flickerAnimation 10s infinite;
}

.blurb-two {
   -webkit-animation: flickerAnimation 3s infinite;
    animation: flickerAnimation 3s infinite;
}


.blurb-three {
   -webkit-animation: flickerAnimation 5s infinite;
    animation: flickerAnimation 5s infinite;
}

